import { API } from 'config/config'
import { Http } from 'services/http'
import { Jwt } from 'services/jwt'

/**
 * Problems (Service)
 * Servicio encargado del manejo de problemas y categoras
 * @export
 * @class Problems
 */
export class Problems {

  /**
   * Mtodo que realiza inyeccin de las dependencias necesarias en el servicio.
   * Estas dependencias son cargadas bajo el patrn de diseo singleton.
   * @static
   * @returns Array con las dependencias a inyectar: Servicio de conexin Http (Http),
   * servicio de manejo de Json Web Tokens (Jwt)
   */
  static inject () {
    return [Http, Jwt]
  }

  /**
   * Crea una instancia de Problems.
   * @param {service} httpService - Servicio de conexin Http (Http)
   * @param {service} jwtService - Servicio de manejo de Json Web Tokens (Jwt)
   */
  constructor (httpService, jwtService) {
    this.jwtService = jwtService
    this.httpService = httpService
  }

 /**
  * Obtiene del backend la lista de categoras existentes.
  * @returns {Promise} Promesa con el token de usuario
  */
  getCategories () {
    return this.httpService.httpClient
      .fetch(API.endponts.categories, {
        method: 'get',
        headers: { 'Authorization': 'Bearer ' + this.jwtService.token }
      })
      .then(this.httpService.checkStatus)
      .then(this.httpService.parseJSON)
  }

  /**
   * Crea una nueva categora en la plataforma.
   * @param {string} name - Nombre de la categora
   * @returns {Promise} Promesa sin body, para validar segn el status.
   */
  createCategory (name) {
    return this.httpService.httpClient
      .fetch(API.endponts.categories, {
        method: 'post',
        headers: { 'Authorization': 'Bearer ' + this.jwtService.token },
        body: {name: name}
      })
      .then(this.httpService.checkStatus)
      .then(this.httpService.parseJSON)
  }

  /**
   * Obtiene la lista de problemas de una categora especfica. Como pueden ser muchos problemas,
   * el mtodo realiza "paginacin" permitiendo traer as solo un nmero determinado de problemas
   * segn los parmetros establecidos.
   * @param {any} id - Identificador de la categora a obtener
   * @param {number} [page=1] - Pgina de resultados a obtener
   * @param {number} [limit=10] - Cantidad de resultados a obtener
   * @param {string} [sort='id'] - Modo de ordenamiento (id o level)
   * @param {string} [by='asc'] - Ordenamiento ascendente o descendente (asc o desc)
   * @returns {Promise} Promesa con los problemas obtenidos
   */
  getCategoryProblems (id, page = 1, limit = 10, sort = 'id', by = 'asc') {
    return this.httpService.httpClient
      .fetch(API.endponts.categoryProblems.replace('{1}', id), {
        method: 'get',
        headers: { 'Authorization': 'Bearer ' + this.jwtService.token },
        body: {
          page: page,
          limit: limit,
          sort: sort,
          by: by
        }
      })
      .then(this.httpService.checkStatus)
      .then(this.httpService.parseJSON)
  }
}
