import { MESSAGES } from 'config/config'
import { Alert, Auth, Problems } from 'services/services'

/**
 * GeneralProblems (Module)
 * Mdulo encargado de la vista principal de las categoras, maratones y problemas
 * @export
 * @class GeneralProblems
 */
export class GeneralProblems {

  /**
   * Mtodo que realiza inyeccin de las dependencias necesarias en el mdulo.
   * Estas dependencias son cargadas bajo el patrn de diseo singleton.
   * @static
   * @returns Array con las dependencias a inyectar: Servicio de notificaciones (Alert),
   * Servicio de Autenticacin (Auth) y Servicio de obtencin y manejo de problemas (Problems)
   */
  static inject () {
    return [Alert, Auth, Problems]
  }

  /**
   * Crea una instancia de GeneralProblems.
   * @param {service} alertService - Servicio de notificaciones
   * @param {service} authService - Servicio de autenticacin
   * @param {service} problemService - Servicio de obtencin y manejo de problemas
   */
  constructor (alertService, authService, problemsService) {
    this.alertService = alertService
    this.authService = authService
    this.problemsService = problemsService
    this.categories = []
    this.newCategory = ''
  }

  /**
   * Inicializa la lista de categoras para desplegarlas en la vista.
   * Este mtodo se dispara una vez la vista y el view-model son cargados.
   */
  created () {
    this.getCategories()
  }

  /**
   * Crea una nueva categora en la plataforma
   */
  createCategory () {
    window.$('#new-category').modal('hide')
    this.problemsService.createCategory(this.newCategory)
      .then(() => {
        this.getCategories()
      })
      .catch(() => {
        this.alertService.showMessage(MESSAGES.unknownError)
      })
  }

  /**
   * Lee la lista de categoras disponibles en la plataforma.
   */
  getCategories () {
    this.problemsService.getCategories()
      .then(data => {
        this.categories = data
      })
      .catch(error => {
        if (error.status === 401) {
          this.alertService.showMessage(MESSAGES.permissionsError)
        } else {
          this.alertService.showMessage(MESSAGES.categoriesError)
        }
      })
  }

}
